import React,{useState} from 'react'
import Data from './../data/movies.json'
import {FaArrowAltCircleRight, FaArrowAltCircleLeft} from 'react-icons/fa'
import movies from './../data/movies.json'
import {NavLink} from "react-router-dom";
const trimExtension = filename => filename.split('.').slice(0, -1)[0];;

const Hero = ({slides}) => {
    const [current, setCurrent] = useState(0)
    const length = slides.length;

    const nextSlider = () => {
        setCurrent(current === length - 1 ? 0 : current + 1)
    }
    const prevtSlider = () => {
        setCurrent(current === 0 ? length - 1 : current - 1)
    }
    // setTimeout( () => {
    //     if((current === length - 1) < length){
    //     setCurrent(current + 1)
    //     }
    // }, 15000);
    if(!Array.isArray(slides) || slides.length <=0){
        return null
    }
    return (
        <>
            <FaArrowAltCircleLeft className='left-arrow' onClick={prevtSlider}/>
            <FaArrowAltCircleRight className='right-arrow' onClick={nextSlider}/>
            {movies.map((slide, index) =>{
            return (
                <div className={index === current ? "slidee active" : 'slidee'} key={index}>
                {index === current && 
                ( 

                <section className='hero featuredMovies has-bullest'
                    style={{backgroundImage:`url(https://image.tmdb.org/t/p/w200${slide.backdropPath})`} } 
                 >
                     
                    <div id='bg'>
                     <div className='container'>
                    <div className='slide featured'>
                    <h2 className="featured-title">{slide.movieLanguages[0].title}</h2>
                    <p className="featured-overview">{slide.movieLanguages[0].overview}
                    </p>
                    <p className='buttons'>
                        <NavLink exact to={`/details${trimExtension(slide.posterPath)}`} className='button is-primary'>
                            شاهد الفلم
                        </NavLink>
                        <NavLink  exact to={`/details${trimExtension(slide.posterPath)}`} className='button '>
                            أقرء المزيد
                        </NavLink>
                    </p>
                    </div>
                </div>
            </div>
                </section>
                
                )}
          
            </div>
            )
            })}
    </>
)
}

export default Hero;