import React from 'react'
import logo from '../img/logo.svg'
import {NavLink} from "react-router-dom";

const Logo = props =>{
    return (
          <NavLink className={props.classes} exact to="/netflux">
            <img src={logo} width='80'/>
          </NavLink>
        )
}
Logo.defaultProps = {
classes:'navbar-item'
}
export default Logo;